@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
/**
 * Neumorphic Timer Stylesheet
 * Inspired by the work of several libraries found online
 * @author: Dory Azar
 * @description: Neumorphism (Neo Skeumoprhism) style of UI
*/

:root {

    --primary: #5e5ce6;
    --primary-theme-2: #0a64ff;
    --primary-theme-3: #8944ab;
    --primary-theme-4: #ff375f;
    --system: #000;
    --success: #248a3e;
    --tint: #FFFFFF;
    --bgLight-1: #f2f2f7;
    --bgLight-2: #e5e5ea;
    --bgDark: #636366;
    --textDark: #636366;
    --textLight: #afafb2;

    /* text sizes */
    --xsmall_text: 0.5rem;
    --small_text: 0.625rem;
    --default_text: 1rem;
    --medium_text: 1.6rem;
    --large_text: 2.5rem;
    --xlarge_text: 3rem;
    --banner_text: 4rem;
}

*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;
    overflow-y: scroll;
    background: #f2f2f7;
    background: var(--bgLight-1);
}
@media screen and (min-width: 900px) {
    html {
        font-size: 75%;
    }
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

table  {
    border: 1px solid #e5e5ea;
    border: 1px solid var(--bgLight-2);
}
tr  {
    border: none;
}
td,td:last-child,
th {
  border: none;
  text-align: left;
  padding: 8px;
}

th {
    border-bottom: 1px solid  #e5e5ea;
    border-bottom: 1px solid  var(--bgLight-2);
}

td {
    border-right: 1px solid  #e5e5ea;
    border-right: 1px solid  var(--bgLight-2);
}

/* GENERIC CLASSES */

.show {
    position: absolute;
    opacity: 1;
    transition: all 0.2s linear;
}

.hide {
    position: absolute;
    opacity: 0;
    transition: all 0.2s linear;
}

/* NAV  BAR */

.navigation {
    width:100%;
    height: 4rem;
    background-color: white;
    position: fixed;
    z-index: 1000;
}

.navigation ul  {
    display: flex;
    flex-direction: row;
    align-content: center;
    align-items: center;
    padding: 0.625rem;
}

.navigation li {
    font-size: 1.6rem;
    font-size: var(--medium_text);
    font-family: "Noto Sans", sans-serif;
    margin: 0 1rem;
    list-style-type: none;
    -webkit-font-feature-settings: "smcp";
            font-feature-settings: "smcp";
    font-variant: small-caps ;
    text-transform: lowercase;

}

.navigation li a, .navigation li a:visited, .navigation li a:active {
    color: #636366;
    color: var(--textDark);
    text-decoration: none;
}

.navigation li a:hover  {
    color: #000;
    color: var(--system);
}

.stopwatch {
    color: #5e5ce6 !important;
    color: var(--primary) !important;
}

.countdown {
    color: #0a64ff !important;
    color: var(--primary-theme-2) !important;
}

.xy {
    color: #8944ab !important;
    color: var(--primary-theme-3) !important;
}

.tabata {
    color: #ff375f !important;
    color: var(--primary-theme-4) !important;
}
/**
 * Neumorphic Card 
 * @author: Dory Azar
*/

.panel_face_front, .panel_face_back {
    position: absolute;
    width: 100%;
    height: 100%;
    backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    overflow: hidden;
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
}

.panel_face_back {
    z-index: 200;
    transform: rotateY(180deg);
    -webkit-transform: rotateY(180deg);
    -moz-transform: rotateY(180deg);
    -ms-transform: rotateY(180deg);
}
/**
 * Neumorphic Display 
 * @author: Dory Azar
*/


.display_circular {
    width: 20rem;
    height: 20rem;
    justify-self: center;
    box-shadow: 0.3rem 0.3rem 0.6rem var(--bgLight-2), -0.2rem -0.2rem 0.5rem var(--tint);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.display_circular .marker {
    width: 97%;
    height: 97%;
    background: var(--bgLight-1);
    border-radius: 50%;
    position: relative;
    box-shadow: inset 0.2rem 0.2rem 0.5rem var(--bgLight-2), inset -0.2rem -0.2rem 0.5rem var(--tint);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 150;
    color: var(--primary);
}

.display_circular p.header {
    margin-bottom: -1rem;
    font-size: var(--medium_text);
    align-self: center;
    justify-self: center;
    -webkit-font-feature-settings: "smcp";
            font-feature-settings: "smcp";
    font-variant: small-caps;
}

.display_circular p.header.stopwatch {
    color: var(--primary);
}

.display_circular p.header.countdown {
    color: var(--primary-theme-2);
}

.display_circular p.header.xy {
    color: var(--primary-theme-3);
}

.display_circular p.header.tabata {
    color: var(--primary-theme-4);
}

.display_circular p.header.success {
    color: var(--success);
}

.display_circular p.value {
    font-size: var(--banner_text);
    align-self: center;
    justify-self: center;
}

.inner_display {
    width: 97%;
    height: 97%;
    background-color: var(--tint);
    position: relative;
    /* box-shadow: inset 1px 1px 1px var(--tint), inset -1px -1px 1px var(--tint); */
    border-radius: 50%;
}

.display_circular_back-1, .display_circular_back-2 {
    width: 12.5rem;
    height: 12.5rem;
    border-radius: 50%;
    -webkit-filter: blur(1px);
            filter: blur(1px);
    z-index: 100;
    align-self: center;
    position: absolute;
}

.display_circular_back-1 {
    box-shadow: 0.4rem 0.4rem 0.8rem var(--bgLight-2), -0.4rem -0.4rem 0.8rem var(--tint);
    background: linear-gradient(to bottom right, var(--bgLight-2) 0%, var(--tint) 100%);
    -webkit-animation: waves 1s linear 0.5s infinite;
            animation: waves 1s linear 0.5s infinite;
}

.display_circular_back-1.paused {
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
}

.display_circular_back-2 {
    box-shadow: 0.4rem 0.4rem 0.8rem var(--bgLight-2), -0.4rem -0.4rem 0.8rem var(--tint);
    -webkit-animation: waves 1s linear 0.5s infinite;
            animation: waves 1s linear 0.5s infinite;
}

.display_circular_back-2.paused {
    -webkit-animation-play-state: paused;
            animation-play-state: paused;
}

.neonText, .neonText.stopwatch  {
    color: var(--primary);
    text-shadow:
        0 0 7px var(--primary),
        0 0 10px var(--tint),
        0 0 21px var(--tint),
        0 0 42px var(--tint),
        0 0 82px var(--tint),
        0 0 92px var(--tint),
        0 0 102px var(--tint),
        0 0 151px var(--tint);
  }

  .neonText.countdown {
    color: var(--primary-theme-2);
    text-shadow:
        0 0 7px var(--primary-theme-2),
        0 0 10px var(--tint),
        0 0 21px var(--tint),
        0 0 42px var(--tint),
        0 0 82px var(--tint),
        0 0 92px var(--tint),
        0 0 102px var(--tint),
        0 0 151px var(--tint);
  }

  .neonText.xy {
    color: var(--primary-theme-3);
    text-shadow:
        0 0 7px var(--primary-theme-3),
        0 0 10px var(--tint),
        0 0 21px var(--tint),
        0 0 42px var(--tint),
        0 0 82px var(--tint),
        0 0 92px var(--tint),
        0 0 102px var(--tint),
        0 0 151px var(--tint);
  }

  .neonText.tabata {
    color: var(--primary-theme-4);
    text-shadow:
        0 0 7px var(--primary-theme-4),
        0 0 10px var(--tint),
        0 0 21px var(--tint),
        0 0 42px var(--tint),
        0 0 82px var(--tint),
        0 0 92px var(--tint),
        0 0 102px var(--tint),
        0 0 151px var(--tint);
  }

  .neonText.success {
      color: var(--success);
      text-shadow:
        0 0 7px var(--success),
        0 0 10px var(--tint),
        0 0 21px var(--tint),
        0 0 42px var(--tint),
        0 0 82px var(--tint),
        0 0 92px var(--tint),
        0 0 102px var(--tint),
        0 0 151px var(--tint);
  }

/* ANIMATIONS */

@-webkit-keyframes waves {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(2);
                transform: scale(2);
        opacity: 0;
    }
}

@keyframes waves {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
    50% {
        opacity: 1;
    }
    100% {
        -webkit-transform: scale(2);
                transform: scale(2);
        opacity: 0;
    }
}
/**
 * Neumorphic Input 
 * @author: Dory Azar
*/

.settings-form {
    width: 50%;
    height: 100%;
    overflow: scroll;
    position: relative;
    display: block;
    align-items: left;
    justify-content:space-evenly;
    flex-direction: column;
}

fieldset {
    display: flex;
    min-height: 9rem;
    border: none;
    flex-direction:column;
    padding: 1rem 0;
}

input {
    max-width: 24rem;
    height: 4rem;
    border: none;
    border-radius: 1rem;
    font-size: var(--medium_text);
    padding-left: 1.4rem;
    box-shadow: inset 0.2rem 0.2rem 0.5rem var(--bgLight-2), inset -0.2rem -0.2rem 0.5rem var(--tint);
    background: none;
    color: var(--textDark);
}

input::placeholder, input::-moz-placeholder, input::-webkit-placeholder, input:-ms-input-placeholder{
    color: var(--textLight);
}

input:focus {
    outline: none;
    box-shadow: 0.3rem 0.3rem 0.6rem var(--bgLight-2), -0.2rem -0.2rem 0.5rem var(--tint);
}

.settings-label {
    font-size: var(--medium_text);
    color: var(--textDark);
    padding-left: 0.5rem;
    padding-bottom: 0.625rem;
}
/**
 * Neumorphic Button 
 * @author: Dory Azar
*/

.btn_bar {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}

.icon_btn {
    width: 4rem;
    height: 4rem;
    margin: 2rem;
    border-radius: 50%;
    box-shadow: 0.3rem 0.3rem 0.6rem var(--bgLight-2), -0.2rem -0.2rem 0.5rem var(--tint);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: var(--medium_text);
    cursor: pointer;
    color: var(--textDark);
    transition: all 0.5s ease;
}

.icon_btn.secondary {
    margin-top: -3.75rem;
    margin-right: -5.75rem;
    margin-top: -10%;
    margin-right: -20%;
}

.icon_btn.primary {
    width: 6rem;
    height: 6rem;
    font-size: var(--large_text);
}
.icon_btn:active {
    box-shadow: inset 0.2rem 0.2rem 0.5rem var(--bgLight-2), inset -0.2rem -0.2rem 0.5rem var(--tint);
    color: var(--primary);
}
.icon_btn:hover, .icon_btn_stopwatch:hover {
    color: var(--primary);
}

.icon_btn_countdown:hover {
    color: var(--primary-theme-2);
}

.icon_btn_xy:hover {
    color: var(--primary-theme-3);
}

.icon_btn_tabata:hover {
    color: var(--primary-theme-4);
}

.icon_btn.selected {
    color: var(--primary);
    box-shadow: inset 0.2rem 0.2rem 0.5rem var(--bgLight-2), inset -0.2rem -0.2rem 0.5rem var(--tint);
}

.icon_btn.disabled {
    cursor: not-allowed;
    pointer-events: none;
    -webkit-filter: opacity(0.5);
            filter: opacity(0.5);
}


/*  BUTTONS  */
.btn {
    width: 15rem;
    height: 4rem;
    border-radius: 1rem;
    box-shadow: 0.3rem 0.3rem 0.6rem var(--bgLight-2), -0.2rem -0.2rem 0.5rem var(--tint);
    justify-self: center;
    display: flex;
    align-items: center;
    justify-content: center;
    align-self: center;
    cursor: pointer;
    transition: 0.3s ease;
    font-size: var(--medium_text);
  }
.btn_primary {
    color: var(--textDark);
    box-shadow: 0.3rem 0.3rem 0.6rem var(--bgLight-2), -0.2rem -0.2rem 0.5rem var(--tint);
}
.btn_primary:hover {
    color: var(--primary);
}

.btn_primary_stopwatch:hover {
    color: var(--primary);
}

.btn_primary_countdown:hover {
    color: var(--primary-theme-2);
}

.btn_primary_xy:hover {
    color: var(--primary-theme-3);
}

.btn_primary_tabata:hover {
    color: var(--primary-theme-4);
}

.btn_primary:active {
    box-shadow: inset 0.2rem 0.2rem 0.5rem var(--bgLight-2), inset -0.2rem -0.2rem 0.5rem var(--tint);
    color: var(--primary);
}
.container {
    background: var(--bgLight-1);
    border-radius: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: "Noto Sans", sans-serif;
    min-height: 100vh;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
/**
 * Neumorphic Tabs 
 * @author: Dory Azar
*/

.tab_control {
    width: 50rem;
    height: 4rem;
    box-shadow: 0.3rem 0.3rem 0.6rem var(--bgLight-2), -0.2rem -0.2rem 0.5rem var(--tint);
    border-radius: 1rem;
    display: flex;
    align-items: center;
    position: relative;
    justify-content: space-evenly;
  }
  .tab_control input {
    display: none;
  }
  .tab_control > input:checked + label {
    transition: all 0.5s ease;
    color: var(--primary);
    font-weight: bold;
  }
  .tab_control_1, .tab_control_2, .tab_control_3, .tab_control_4 {
    width: 8rem;
    height: 3.6rem;
    font-size: var(--medium_text);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color: var(--textDark);
    transition: all 0.5s ease;
    margin-left: 2rem;
  }
  .tab_control_1:hover, .tab_control_2:hover, .tab_control_3:hover, .tab_control_4:hover{
    color: var(--primary);
  }
  .tab_control_color {
    position: absolute;
    height: 3.4rem;
    width: 11rem;
    margin-left: 0.3rem;
    border-radius: 0.8rem;
    box-shadow: inset 0.2rem 0.2rem 0.5rem var(--bgLight-2), inset -0.2rem -0.2rem 0.5rem var(--tint);
    pointer-events: none;
  }
  
  #tab-1:checked ~ .tab_control_color {
    -webkit-transform: translateX(-17rem);
            transform: translateX(-17rem);
    transition: -webkit-transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), -webkit-transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  
  #tab-2:checked ~ .tab_control_color {
    -webkit-transform: translateX(-5rem);
            transform: translateX(-5rem);
    transition: -webkit-transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), -webkit-transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  }

  #tab-3:checked ~ .tab_control_color {
    -webkit-transform: translateX(7rem);
            transform: translateX(7rem);
    transition: -webkit-transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), -webkit-transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  }

  #tab-4:checked ~ .tab_control_color {
    -webkit-transform: translateX(19rem);
            transform: translateX(19rem);
    transition: -webkit-transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
    transition: transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), -webkit-transform 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  }
/**
 * Neumorphic Panel 
 * @author: Dory Azar
*/

.panel {
    width: 50rem;
    margin: 2rem;
    min-width: 35rem;
    height: 40rem;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
}

.panel_inner {
    overflow: visible !important;
    width: 100%;
    height: 100%;
    border-radius: 3rem;
    box-shadow: 0.8rem 0.8rem 1.4rem var(--bgLight-2), -0.2rem -0.2rem 1.8rem var(--tint);
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    transform-style: preserve-3d;
    -webkit-transform-style: preserve-3d;
    -moz-transform-style: preserve-3d;
    -ms-transform-style: preserve-3d;
}

.panel_inner.is-flipped {
    transform: rotateY(180deg);
    -webkit-transform: rotateY(180deg);
    -moz-transform: rotateY(180deg);
    -ms-transform: rotateY(180deg);
}

.panel h1 {
    font-size: var(--large_text);
    color: var(--textDark);
    margin-top: 1rem;
}


img{
    max-height: 20rem;
    width: auto;
}
